body {
  margin: 0;
  padding: 0;
}

img,
a {
  cursor: pointer;
}

button {
  background: #743ad5;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1.2em;
  margin: 20px auto;
}

@media screen and (max-width: 1000px) {
  button {
    margin: 5px auto;
  }
}
